import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { ref, get, query, orderByChild, limitToLast } from 'firebase/database';
import { database } from '../firebase';

const Home = () => {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [featuredPost, setFeaturedPost] = useState(null);

  useEffect(() => {
    const fetchPosts = async () => {
      try {
        const postsRef = ref(database, 'posts');
        const snapshot = await get(postsRef);

        if (snapshot.exists()) {
          const postsData = snapshot.val();
          const postsArray = Object.keys(postsData).map(id => ({
            id,
            ...postsData[id]
          }));

          // Sp xp theo thi gian to gim dn (mi nht trc)
          postsArray.sort((a, b) => b.createdAt - a.createdAt);

          // Chn bi vit ni bt (bi u tin hoc bi c lt xem cao nht)
          const mostViewedPost = [...postsArray].sort((a, b) => (b.viewCount || 0) - (a.viewCount || 0))[0];
          setFeaturedPost(mostViewedPost || postsArray[0]);

          // Lc b bi vit ni bt khi danh sch cc bi vit thng thng
          const regularPosts = postsArray.filter(post => post.id !== (mostViewedPost || postsArray[0]).id);
          setPosts(regularPosts);
        } else {
          setPosts([]);
        }
      } catch (error) {
        console.error('Li khi ly bi vit:', error);
        setError('Khng th ti bi vit. Vui lng th li sau.');
      } finally {
        setLoading(false);
      }
    };

    fetchPosts();
  }, []);

  // Hm rt gn ni dung
  const truncateContent = (content, maxLength = 120) => {
    if (!content) return '';
    if (content.length <= maxLength) return content;
    return content.substring(0, maxLength) + '...';
  };

  // nh dng ngy thng
  const formatDate = (timestamp) => {
    if (!timestamp) return '';
    const date = new Date(timestamp);
    return date.toLocaleDateString('vi-VN', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  return (
    <div className="home-container fade-in">
      <div className="home-header">
        <h1 className="home-title">WebAPPP Blog</h1>
        <p className="home-subtitle">
          Khm ph cc bi vit cht lng v cng ngh, tin tc v nhiu ch  th v khc
        </p>
      </div>

      {error && <div className="error-message">{error}</div>}

      {loading ? (
        <div className="loading-spinner">
          <div className="spinner"></div>
        </div>
      ) : (
        <>
          {/* Featured Post Section */}
          {featuredPost && (
            <div className="featured-post">
              <h2 className="posts-section-title">Bi vit ni bt</h2>
              <div className="featured-post-card">
                <div className="featured-post-image">
                  {featuredPost.imageUrl ? (
                    <img src={featuredPost.imageUrl} alt={featuredPost.title} />
                  ) : (
                    <div className="placeholder-image" style={{background: 'linear-gradient(45deg, var(--primary-dark), var(--primary))'}}></div>
                  )}
                  <span className="featured-label">Ni bt</span>
                </div>
                <div className="featured-post-content">
                  <h2 className="featured-post-title">
                    <Link to={`/post/${featuredPost.id}`}>{featuredPost.title}</Link>
                  </h2>
                  <div className="featured-post-excerpt">
                    {truncateContent(featuredPost.content, 180)}
                  </div>
                  <div className="post-card-meta">
                    <span>
                      <i className="far fa-calendar"></i> {formatDate(featuredPost.createdAt)}
                    </span>
                    <span>
                      <i className="far fa-eye"></i> {featuredPost.viewCount || 0} lt xem
                    </span>
                    {featuredPost.author && (
                      <span>
                        <i className="far fa-user"></i> {featuredPost.author}
                      </span>
                    )}
                  </div>
                  <div className="post-card-actions">
                    <Link to={`/post/${featuredPost.id}`} className="btn btn-primary">
                      c tip <i className="fas fa-arrow-right"></i>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Regular Posts Section */}
          <div className="posts-section">
            <h2 className="posts-section-title">Bi vit mi nht</h2>
            <div className="post-grid">
              {posts.length > 0 ? (
                posts.map(post => (
                  <div className="post-card" key={post.id}>
                    <div className="post-card-image">
                      {post.imageUrl ? (
                        <img src={post.imageUrl} alt={post.title} />
                      ) : (
                        <div className="placeholder-image" style={{background: 'linear-gradient(135deg, var(--primary-dark), var(--secondary-dark))'}}></div>
                      )}
                      {post.category && (
                        <span className="post-card-category">{post.category}</span>
                      )}
                    </div>
                    <div className="post-card-content">
                      <h3>
                        <Link to={`/post/${post.id}`}>{post.title}</Link>
                      </h3>
                      <div className="post-card-excerpt">
                        {truncateContent(post.content)}
                      </div>
                      <div className="post-card-meta">
                        <span>
                          <i className="far fa-calendar"></i> {formatDate(post.createdAt)}
                        </span>
                        <span>
                          <i className="far fa-eye"></i> {post.viewCount || 0}
                        </span>
                      </div>
                      <div className="post-card-actions">
                        <Link to={`/post/${post.id}`} className="btn btn-secondary btn-sm">
                          <i className="fas fa-book-open"></i> c tip
                        </Link>
                      </div>
                    </div>
                  </div>
                ))
              ) : (
                <div className="no-posts">
                  <p>Cha c bi vit no.</p>
                </div>
              )}
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default Home;
