import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { ref, onValue, remove } from 'firebase/database';
import { database } from '../../firebase';

const AdminPosts = () => {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    const postsRef = ref(database, 'posts');

    const unsubscribe = onValue(postsRef, (snapshot) => {
      setLoading(true);

      try {
        const postsData = snapshot.val() || {};

        // Chuyn i i tng posts thnh mng v sp xp theo thi gian to (mi nht trc)
        const postsArray = Object.keys(postsData).map(id => ({
          id,
          ...postsData[id]
        })).sort((a, b) => b.createdAt - a.createdAt);

        setPosts(postsArray);
        setError('');
      } catch (error) {
        console.error('Li khi ti d liu bi vit:', error);
        setError('Khng th ti d liu bi vit. Vui lng th li sau.');
      } finally {
        setLoading(false);
      }
    });

    // Cleanup function
    return () => unsubscribe();
  }, []);

  const handleDeletePost = async (postId) => {
    if (window.confirm('Bn c chc chn mun xa bi vit ny?')) {
      try {
        // Xa bi vit
        await remove(ref(database, `posts/${postId}`));

        // Xa bnh lun lin quan
        await remove(ref(database, `comments/${postId}`));

        // Xa nh gi lin quan
        await remove(ref(database, `ratings/${postId}`));

        // Khng cn cp nht state v onValue listener s t ng cp nht

      } catch (error) {
        console.error('Li khi xa bi vit:', error);
        setError('Khng th xa bi vit. Vui lng th li sau.');
      }
    }
  };

  const formatDate = (timestamp) => {
    if (!timestamp) return '';
    const date = new Date(timestamp);
    return date.toLocaleDateString('vi-VN', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  };

  return (
    <div className="admin-posts-container">
      <div className="header-actions">
        <h1>Qun l bi vit</h1>
        <Link to="/admin/posts/create" className="btn btn-success">To bi vit mi</Link>
      </div>

      {error && <div className="error-message">{error}</div>}

      {loading ? (
        <div className="loading">
          <div className="spinner"></div>
        </div>
      ) : posts.length > 0 ? (
        <div className="admin-posts-table">
          <table>
            <thead>
              <tr>
                <th>Tiu </th>
                <th>Danh mc</th>
                <th>Ngy ng</th>
                <th>Lt xem</th>
                <th>nh gi</th>
                <th>Thao tc</th>
              </tr>
            </thead>
            <tbody>
              {posts.map(post => (
                <tr key={post.id}>
                  <td><Link to={`/post/${post.id}`}>{post.title}</Link></td>
                  <td>{post.category}</td>
                  <td>{formatDate(post.createdAt)}</td>
                  <td>{post.viewCount || 0}</td>
                  <td>
                    {post.averageRating ? (
                      <span> {post.averageRating.toFixed(1)} ({post.ratingCount})</span>
                    ) : (
                      <span>Cha c nh gi</span>
                    )}
                  </td>
                  <td className="actions">
                    <Link to={`/admin/posts/edit/${post.id}`} className="btn-edit">Sa</Link>
                    <button
                      onClick={() => handleDeletePost(post.id)}
                      className="btn-delete"
                    >
                      Xa
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : (
        <p className="no-posts">Cha c bi vit no.</p>
      )}
    </div>
  );
};

export default AdminPosts;
