import React, { useEffect } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Home from './pages/Home';
import PostDetail from './pages/PostDetail';
import Login from './pages/Login';
import Register from './pages/Register';
import AdminDashboard from './pages/admin/AdminDashboard';
import AdminPosts from './pages/admin/AdminPosts';
import CreatePost from './pages/admin/CreatePost';
import EditPost from './pages/admin/EditPost';
import Profile from './pages/Profile';
import NotFound from './pages/NotFound';
import { AuthProvider } from './context/AuthContext';
import ProtectedRoute from './components/ProtectedRoute';
import AdminRoute from './components/AdminRoute';
import Header from './components/Header';
import Footer from './components/Footer';
import Search from './pages/Search';
import './App.css';

function App() {
  // X l thanh progress khi cun trang
  useEffect(() => {
    const handleScroll = () => {
      const totalScroll = document.documentElement.scrollHeight - document.documentElement.clientHeight;
      const scrollPosition = window.scrollY;
      const scrollProgress = (scrollPosition / totalScroll) * 100;

      const progressBar = document.querySelector('.scroll-progress-bar');
      if (progressBar) {
        progressBar.style.width = `${scrollProgress}%`;
      }

      // Hin th/n nt scroll to top
      const scrollToTopBtn = document.querySelector('.scroll-to-top');
      if (scrollToTopBtn) {
        if (scrollPosition > 300) {
          scrollToTopBtn.classList.add('visible');

          // Thm hiu ng pulse khi gn cui trang
          if (scrollProgress > 80) {
            scrollToTopBtn.classList.add('pulse');
          } else {
            scrollToTopBtn.classList.remove('pulse');
          }
        } else {
          scrollToTopBtn.classList.remove('visible');
          scrollToTopBtn.classList.remove('pulse');
        }
      }
    };

    // Thm scroll event listener
    window.addEventListener('scroll', handleScroll);

    // X l smooth scroll khi click vo lin kt
    const setupSmoothScroll = () => {
      document.querySelectorAll('a[href^="#"]').forEach(anchor => {
        anchor.addEventListener('click', function(e) {
          e.preventDefault();
          const target = document.querySelector(this.getAttribute('href'));
          if (target) {
            window.scrollTo({
              top: target.offsetTop - 70, // Tr chiu cao ca header
              behavior: 'smooth'
            });
          }
        });
      });
    };

    setupSmoothScroll();

    // Clean up event listeners
    return () => {
      window.removeEventListener('scroll', handleScroll);
      document.querySelectorAll('a[href^="#"]').forEach(anchor => {
        anchor.removeEventListener('click', function() {});
      });
    };
  }, []);

  return (
    <AuthProvider>
      <BrowserRouter>
        <div className="app" id="top">
          <Header />
          <main className="container">
            <Routes>
              {/* Trang cng khai */}
              <Route path="/" element={<Home />} />
              <Route path="/post/:id" element={<PostDetail />} />
              <Route path="/login" element={<Login />} />
              <Route path="/register" element={<Register />} />
              <Route path="/search" element={<Search />} />

              {/* Trang yu cu ng nhp */}
              <Route path="/profile" element={
                <ProtectedRoute>
                  <Profile />
                </ProtectedRoute>
              } />

              {/* Trang Admin */}
              <Route path="/admin" element={
                <AdminRoute>
                  <AdminDashboard />
                </AdminRoute>
              } />
              <Route path="/admin/posts" element={
                <AdminRoute>
                  <AdminPosts />
                </AdminRoute>
              } />
              <Route path="/admin/posts/create" element={
                <AdminRoute>
                  <CreatePost />
                </AdminRoute>
              } />
              <Route path="/admin/posts/edit/:id" element={
                <AdminRoute>
                  <EditPost />
                </AdminRoute>
              } />

              {/* Trang khng tm thy */}
              <Route path="*" element={<NotFound />} />
            </Routes>
          </main>
          <Footer />

          {/* Nt scroll to top */}
          <button className="scroll-to-top" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}>
            <i className="fas fa-arrow-up"></i>
          </button>
        </div>
      </BrowserRouter>
    </AuthProvider>
  );
}

export default App;
