import React, { useState, useEffect } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import SearchBar from './SearchBar';

const Header = () => {
  const { currentUser, isAdmin, logout } = useAuth();
  const navigate = useNavigate();
  const location = useLocation();
  const [scrolled, setScrolled] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);

  // X l s kin scroll  thay i hiu ng header
  useEffect(() => {
    const handleScroll = () => {
      const offset = window.scrollY;
      if (offset > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  // X l ng xut
  const handleLogout = async () => {
    try {
      await logout();
      navigate('/');
    } catch (error) {
      console.error('Li khi ng xut:', error);
    }
  };

  // Kim tra xem lin kt c ang active khng
  const isActive = (path) => {
    return location.pathname === path;
  };

  // ng menu khi chn lin kt (trn mobile)
  const closeMenu = () => {
    setMenuOpen(false);
  };

  // X l tm kim
  const handleSearch = (query) => {
    navigate(`/search?q=${encodeURIComponent(query)}`);
    closeMenu();
  };

  return (
    <header className={scrolled ? 'scrolled' : ''}>
      <div className="container">
        <nav className="navbar">
          <Link to="/" className="brand">
            <span className="gradient-text">WebAPPP</span> Blog
          </Link>

          <div className="navbar-center">
            <ul className={`nav-links ${menuOpen ? 'active' : ''}`}>
              <li>
                <Link
                  to="/"
                  className={isActive('/') ? 'active' : ''}
                  onClick={closeMenu}
                >
                  <i className="fas fa-home"></i> Trang ch
                </Link>
              </li>

              {isAdmin && (
                <li>
                  <Link
                    to="/admin"
                    className={isActive('/admin') ? 'active' : ''}
                    onClick={closeMenu}
                  >
                    <i className="fas fa-cogs"></i> Qun tr
                  </Link>
                </li>
              )}

              {currentUser ? (
                <>
                  <li>
                    <Link
                      to="/profile"
                      className={isActive('/profile') ? 'active' : ''}
                      onClick={closeMenu}
                    >
                      <i className="fas fa-user"></i> H s
                    </Link>
                  </li>
                  <li>
                    <button
                      onClick={handleLogout}
                      className="nav-button"
                    >
                      <i className="fas fa-sign-out-alt"></i> ng xut
                    </button>
                  </li>
                </>
              ) : (
                <>
                  <li>
                    <Link
                      to="/login"
                      className={isActive('/login') ? 'active' : ''}
                      onClick={closeMenu}
                    >
                      <i className="fas fa-sign-in-alt"></i> ng nhp
                    </Link>
                  </li>
                  <li>
                    <Link
                      to="/register"
                      className={`nav-button-highlighted ${isActive('/register') ? 'active' : ''}`}
                      onClick={closeMenu}
                    >
                      <i className="fas fa-user-plus"></i> ng k
                    </Link>
                  </li>
                </>
              )}
            </ul>
          </div>

          <div className="navbar-right">
            <SearchBar onSearch={handleSearch} />

            {/* Menu toggle button cho mobile */}
            <div
              className={`menu-toggle ${menuOpen ? 'active' : ''}`}
              onClick={() => setMenuOpen(!menuOpen)}
            >
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
        </nav>
      </div>

      {/* Thanh tin trnh cun */}
      <div className="scroll-progress">
        <div className="scroll-progress-bar"></div>
      </div>
    </header>
  );
};

export default Header;
