import React, { useState, useEffect } from 'react';
import { Link, useSearchParams } from 'react-router-dom';
import { ref, get, query, orderByChild } from 'firebase/database';
import { database } from '../firebase';

const Search = () => {
  const [searchParams] = useSearchParams();
  const searchQuery = searchParams.get('q') || '';

  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const searchPosts = async () => {
      if (!searchQuery.trim()) {
        setPosts([]);
        setLoading(false);
        return;
      }

      try {
        setLoading(true);
        const postsRef = ref(database, 'posts');
        const snapshot = await get(postsRef);

        if (snapshot.exists()) {
          const postsData = snapshot.val();
          let postsArray = Object.keys(postsData).map(id => ({
            id,
            ...postsData[id]
          }));

          // Lc bi vit theo t kha tm kim
          const filteredPosts = postsArray.filter(post => {
            const titleMatch = post.title?.toLowerCase().includes(searchQuery.toLowerCase());
            const contentMatch = post.content?.toLowerCase().includes(searchQuery.toLowerCase());
            const authorMatch = post.author?.toLowerCase().includes(searchQuery.toLowerCase());
            const categoryMatch = post.category?.toLowerCase().includes(searchQuery.toLowerCase());

            return titleMatch || contentMatch || authorMatch || categoryMatch;
          });

          setPosts(filteredPosts);
        } else {
          setPosts([]);
        }
      } catch (error) {
        console.error('Li khi tm kim bi vit:', error);
        setError('Khng th tm kim bi vit. Vui lng th li sau.');
      } finally {
        setLoading(false);
      }
    };

    searchPosts();
  }, [searchQuery]);

  // Hm rt gn ni dung
  const truncateContent = (content, maxLength = 150) => {
    if (!content) return '';
    if (content.length <= maxLength) return content;
    return content.substring(0, maxLength) + '...';
  };

  // nh dng ngy thng
  const formatDate = (timestamp) => {
    if (!timestamp) return '';
    const date = new Date(timestamp);
    return date.toLocaleDateString('vi-VN', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  // Hm highlight t kha tm kim
  const highlightText = (text, query) => {
    if (!text || !query.trim()) return text;

    const regex = new RegExp(`(${query.trim()})`, 'gi');
    return text.replace(regex, '<mark>$1</mark>');
  };

  return (
    <div className="search-results-container fade-in">
      <div className="search-header">
        <h1 className="search-title">Kt qu tm kim</h1>
        <p className="search-subtitle">
          {posts.length > 0
            ? `Tm thy ${posts.length} kt qu cho "${searchQuery}"`
            : loading
              ? 'ang tm kim...'
              : `Khng tm thy kt qu no cho "${searchQuery}"`
          }
        </p>
      </div>

      {error && <div className="error-message">{error}</div>}

      {loading ? (
        <div className="loading-spinner">
          <div className="spinner"></div>
        </div>
      ) : (
        <div className="search-results">
          {posts.length > 0 ? (
            posts.map(post => (
              <div className="search-result-item" key={post.id}>
                <div className="search-result-content">
                  <h2>
                    <Link to={`/post/${post.id}`}
                      dangerouslySetInnerHTML={{
                        __html: highlightText(post.title, searchQuery)
                      }}
                    />
                  </h2>
                  <div className="search-result-excerpt"
                    dangerouslySetInnerHTML={{
                      __html: highlightText(truncateContent(post.content), searchQuery)
                    }}
                  />
                  <div className="search-result-meta">
                    {post.category && (
                      <span className="search-result-category">{post.category}</span>
                    )}
                    <span>
                      <i className="far fa-calendar"></i> {formatDate(post.createdAt)}
                    </span>
                    {post.author && (
                      <span>
                        <i className="far fa-user"></i> {post.author}
                      </span>
                    )}
                  </div>
                </div>
                {post.imageUrl && (
                  <div className="search-result-image">
                    <img src={post.imageUrl} alt={post.title} />
                  </div>
                )}
              </div>
            ))
          ) : (
            <div className="no-results">
              <div className="no-results-icon">
                <i className="fas fa-search"></i>
              </div>
              <p>Khng tm thy kt qu no cho tm kim ca bn.</p>
              <p>Vui lng th li vi t kha khc hoc tr v <Link to="/">trang ch</Link>.</p>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default Search;
