import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { ref, get, update, serverTimestamp } from 'firebase/database';
import { ref as storageRef, uploadBytes, getDownloadURL } from 'firebase/storage';
import { database, storage } from '../../firebase';
import { useAuth } from '../../context/AuthContext';

const EditPost = () => {
  const { id } = useParams();
  const { currentUser } = useAuth();
  const navigate = useNavigate();

  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [category, setCategory] = useState('');
  const [tags, setTags] = useState('');
  const [imageUrl, setImageUrl] = useState('');
  const [image, setImage] = useState(null);
  const [imagePreview, setImagePreview] = useState('');
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    const fetchPost = async () => {
      try {
        const postRef = ref(database, `posts/${id}`);
        const snapshot = await get(postRef);

        if (snapshot.exists()) {
          const postData = snapshot.val();

          setTitle(postData.title || '');
          setContent(postData.content || '');
          setCategory(postData.category || '');
          setTags((postData.tags || []).join(', '));
          setImageUrl(postData.imageUrl || '');
          setImagePreview(postData.imageUrl || '');
        } else {
          setError('Khng tm thy bi vit');
        }
      } catch (error) {
        console.error('Li khi ti bi vit:', error);
        setError('Khng th ti thng tin bi vit. Vui lng th li sau.');
      } finally {
        setLoading(false);
      }
    };

    fetchPost();
  }, [id]);

  const handleImageChange = (e) => {
    if (e.target.files[0]) {
      const file = e.target.files[0];
      setImage(file);

      // To URL xem trc cho hnh nh
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Kim tra cc trng bt buc
    if (!title || !content || !category) {
      setError('Vui lng in y  thng tin (tiu , ni dung, danh mc)');
      return;
    }

    setSaving(true);
    setError('');

    try {
      let updatedImageUrl = imageUrl;

      // Nu c hnh nh mi, ti ln Cloud Storage
      if (image) {
        const imageStorageRef = storageRef(storage, `images/${Date.now()}_${image.name}`);
        await uploadBytes(imageStorageRef, image);
        updatedImageUrl = await getDownloadURL(imageStorageRef);
      }

      // Cp nht bi vit
      const postRef = ref(database, `posts/${id}`);

      await update(postRef, {
        title,
        content,
        category,
        tags: tags.split(',').map(tag => tag.trim()).filter(tag => tag),
        imageUrl: updatedImageUrl,
        updatedAt: serverTimestamp()
      });

      // Chuyn hng n trang qun l bi vit
      navigate('/admin/posts');

    } catch (error) {
      console.error('Li khi cp nht bi vit:', error);
      setError('Khng th cp nht bi vit. Vui lng th li sau.');
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <div className="loading">
        <div className="spinner"></div>
      </div>
    );
  }

  return (
    <div className="edit-post-container">
      <h1>Chnh sa bi vit</h1>

      {error && <div className="error-message">{error}</div>}

      <form onSubmit={handleSubmit} className="post-form">
        <div className="form-group">
          <label htmlFor="title">Tiu  *</label>
          <input
            type="text"
            id="title"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="category">Danh mc *</label>
          <input
            type="text"
            id="category"
            value={category}
            onChange={(e) => setCategory(e.target.value)}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="tags">Th (cch nhau bi du phy)</label>
          <input
            type="text"
            id="tags"
            value={tags}
            onChange={(e) => setTags(e.target.value)}
            placeholder="Tin tc, Cng ngh, ..."
          />
        </div>

        <div className="form-group">
          <label htmlFor="image">Hnh nh</label>
          <input
            type="file"
            id="image"
            onChange={handleImageChange}
            accept="image/*"
          />
          {imagePreview && (
            <div className="image-preview">
              <img src={imagePreview} alt="Preview" />
            </div>
          )}
        </div>

        <div className="form-group">
          <label htmlFor="content">Ni dung *</label>
          <textarea
            id="content"
            value={content}
            onChange={(e) => setContent(e.target.value)}
            rows={15}
            required
          ></textarea>
        </div>

        <div className="form-actions">
          <button type="button" onClick={() => navigate('/admin/posts')} className="btn-secondary">
            Hy b
          </button>
          <button type="submit" disabled={saving} className="btn-primary">
            {saving ? 'ang lu...' : 'Lu thay i'}
          </button>
        </div>
      </form>
    </div>
  );
};

export default EditPost;
