import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { ref, push, update } from 'firebase/database';
import { ref as storageRef, uploadBytes, getDownloadURL } from 'firebase/storage';
import { database, storage } from '../../firebase';
import { useAuth } from '../../context/AuthContext';

const CreatePost = () => {
  const { currentUser, userRole, isAdmin } = useAuth();
  const navigate = useNavigate();

  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [category, setCategory] = useState('');
  const [tags, setTags] = useState('');
  const [image, setImage] = useState(null);
  const [imagePreview, setImagePreview] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    console.log("CreatePost - Current user:", currentUser);
    console.log("CreatePost - User role:", userRole);
    console.log("CreatePost - Is admin:", isAdmin);
  }, [currentUser, userRole, isAdmin]);

  const handleImageChange = (e) => {
    if (e.target.files[0]) {
      const file = e.target.files[0];
      setImage(file);

      // To URL xem trc cho hnh nh
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log("Submit button clicked");

    // Kim tra cc trng bt buc
    if (!title || !content || !category) {
      setError('Vui lng in y  thng tin (tiu , ni dung, danh mc)');
      return;
    }

    console.log("Validation passed, preparing to create post");
    setLoading(true);
    setError('');

    try {
      console.log("Current user before creating post:", currentUser);
      if (!currentUser || !isAdmin) {
        console.error("User not authenticated or not admin");
        setError('Bn khng c quyn to bi vit');
        setLoading(false);
        return;
      }

      let imageUrl = '';

      // Nu c hnh nh, ti ln Cloud Storage
      if (image) {
        console.log("Uploading image to Firebase Storage");
        const imageStorageRef = storageRef(storage, `images/${Date.now()}_${image.name}`);
        await uploadBytes(imageStorageRef, image);
        imageUrl = await getDownloadURL(imageStorageRef);
        console.log("Image uploaded successfully:", imageUrl);
      }

      // To bi vit mi
      console.log("Creating new post in Firebase Realtime Database");
      const postsRef = ref(database, 'posts');
      const newPostRef = push(postsRef);

      // S dng update thay v set
      const updates = {};
      updates[`/posts/${newPostRef.key}`] = {
        title,
        content,
        category,
        tags: tags.split(',').map(tag => tag.trim()).filter(tag => tag),
        imageUrl,
        author: currentUser.uid,
        authorName: currentUser.displayName || 'Admin',
        createdAt: Date.now(),
        updatedAt: Date.now(),
        viewCount: 0,
        averageRating: 0,
        ratingCount: 0
      };

      // Cp nht database
      await update(ref(database), updates);

      console.log("Post created successfully");
      // Chuyn hng n trang qun l bi vit
      navigate('/admin/posts');

    } catch (error) {
      console.error('Li khi to bi vit:', error);
      setError('Khng th to bi vit. Vui lng th li sau.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="create-post-container">
      <h1>To bi vit mi</h1>

      {error && <div className="error-message">{error}</div>}

      <form onSubmit={handleSubmit} className="post-form">
        <div className="form-group">
          <label htmlFor="title">Tiu  *</label>
          <input
            type="text"
            id="title"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="category">Danh mc *</label>
          <input
            type="text"
            id="category"
            value={category}
            onChange={(e) => setCategory(e.target.value)}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="tags">Th (cch nhau bi du phy)</label>
          <input
            type="text"
            id="tags"
            value={tags}
            onChange={(e) => setTags(e.target.value)}
            placeholder="Tin tc, Cng ngh, ..."
          />
        </div>

        <div className="form-group">
          <label htmlFor="image">Hnh nh</label>
          <input
            type="file"
            id="image"
            onChange={handleImageChange}
            accept="image/*"
          />
          {imagePreview && (
            <div className="image-preview">
              <img src={imagePreview} alt="Preview" />
            </div>
          )}
        </div>

        <div className="form-group">
          <label htmlFor="content">Ni dung *</label>
          <textarea
            id="content"
            value={content}
            onChange={(e) => setContent(e.target.value)}
            rows={15}
            required
          ></textarea>
        </div>

        <div className="form-actions">
          <button type="button" onClick={() => navigate('/admin/posts')} className="btn-secondary">
            Hy b
          </button>
          <button type="submit" disabled={loading} className="btn-primary">
            {loading ? 'ang to...' : 'To bi vit'}
          </button>
        </div>
      </form>
    </div>
  );
};

export default CreatePost;
