import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

const Register = () => {
  const { register } = useAuth();
  const navigate = useNavigate();

  const [displayName, setDisplayName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!displayName || !email || !password || !confirmPassword) {
      setError('Vui lng nhp y  thng tin.');
      return;
    }

    if (password !== confirmPassword) {
      setError('Mt khu xc nhn khng khp.');
      return;
    }

    if (password.length < 6) {
      setError('Mt khu phi c t nht 6 k t.');
      return;
    }

    try {
      setError('');
      setLoading(true);
      await register(email, password, displayName);
      navigate('/');
    } catch (error) {
      console.error('Li ng k:', error);

      switch (error.code) {
        case 'auth/email-already-in-use':
          setError('Email  c s dng.');
          break;
        case 'auth/invalid-email':
          setError('Email khng hp l.');
          break;
        case 'auth/weak-password':
          setError('Mt khu qu yu. Vui lng chn mt khu mnh hn.');
          break;
        default:
          setError('ng k tht bi. Vui lng th li sau.');
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="auth-container">
      <div className="auth-form-container">
        <h2 className="text-center mb-3">ng k ti khon</h2>

        {error && <div className="error-message">{error}</div>}

        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="displayName">H v tn</label>
            <input
              type="text"
              id="displayName"
              value={displayName}
              onChange={(e) => setDisplayName(e.target.value)}
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="email">Email</label>
            <input
              type="email"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="password">Mt khu</label>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
            <small>Mt khu phi c t nht 6 k t.</small>
          </div>

          <div className="form-group">
            <label htmlFor="confirmPassword">Xc nhn mt khu</label>
            <input
              type="password"
              id="confirmPassword"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              required
            />
          </div>

          <button type="submit" disabled={loading}>
            {loading ? 'ang ng k...' : 'ng k'}
          </button>
        </form>

        <div className="auth-links text-center mt-3">
          <p>
             c ti khon? <Link to="/login">ng nhp</Link>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Register;
