import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

const Login = () => {
  const { login } = useAuth();
  const navigate = useNavigate();

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!email || !password) {
      setError('Vui lng nhp y  thng tin.');
      return;
    }

    try {
      setError('');
      setLoading(true);
      await login(email, password);
      navigate('/');
    } catch (error) {
      console.error('Li ng nhp:', error);

      switch (error.code) {
        case 'auth/invalid-email':
          setError('Email khng hp l.');
          break;
        case 'auth/user-disabled':
          setError('Ti khon  b v hiu ha.');
          break;
        case 'auth/user-not-found':
          setError('Email khng tn ti.');
          break;
        case 'auth/wrong-password':
          setError('Mt khu khng chnh xc.');
          break;
        default:
          setError('ng nhp tht bi. Vui lng th li sau.');
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="auth-container">
      <div className="auth-form-container">
        <h2 className="text-center mb-3">ng nhp</h2>

        {error && <div className="error-message">{error}</div>}

        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="email">Email</label>
            <input
              type="email"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="password">Mt khu</label>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
          </div>

          <button type="submit" disabled={loading}>
            {loading ? 'ang ng nhp...' : 'ng nhp'}
          </button>
        </form>

        <div className="auth-links text-center mt-3">
          <p>
            Cha c ti khon? <Link to="/register">ng k ngay</Link>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Login;
