import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { ref, onValue } from 'firebase/database';
import { database } from '../../firebase';

const AdminDashboard = () => {
  const [stats, setStats] = useState({
    totalPosts: 0,
    totalUsers: 0,
    totalComments: 0,
    totalViews: 0
  });

  useEffect(() => {
    const fetchStats = async () => {
      // Ly s lng bi vit
      const postsRef = ref(database, 'posts');
      onValue(postsRef, (snapshot) => {
        const posts = snapshot.val() || {};
        const postsCount = Object.keys(posts).length;

        let totalViews = 0;
        Object.values(posts).forEach(post => {
          totalViews += post.viewCount || 0;
        });

        setStats(prevStats => ({
          ...prevStats,
          totalPosts: postsCount,
          totalViews
        }));
      });

      // Ly s lng ngi dng
      const usersRef = ref(database, 'users');
      onValue(usersRef, (snapshot) => {
        const users = snapshot.val() || {};
        const usersCount = Object.keys(users).length;

        setStats(prevStats => ({
          ...prevStats,
          totalUsers: usersCount
        }));
      });

      // Ly s lng bnh lun
      const commentsRef = ref(database, 'comments');
      onValue(commentsRef, (snapshot) => {
        const commentsData = snapshot.val() || {};
        let commentsCount = 0;

        Object.values(commentsData).forEach(postComments => {
          commentsCount += Object.keys(postComments).length;
        });

        setStats(prevStats => ({
          ...prevStats,
          totalComments: commentsCount
        }));
      });
    };

    fetchStats();
  }, []);

  return (
    <div className="admin-dashboard-container">
      <h1>Bng iu khin Admin</h1>

      <div className="admin-actions">
        <Link to="/admin/posts" className="btn btn-primary">Qun l bi vit</Link>
        <Link to="/admin/posts/create" className="btn btn-success">To bi vit mi</Link>
      </div>

      <div className="admin-dashboard">
        <div className="stat-card">
          <div className="stat-value">{stats.totalPosts}</div>
          <div className="stat-label">Bi vit</div>
        </div>

        <div className="stat-card">
          <div className="stat-value">{stats.totalUsers}</div>
          <div className="stat-label">Ngi dng</div>
        </div>

        <div className="stat-card">
          <div className="stat-value">{stats.totalComments}</div>
          <div className="stat-label">Bnh lun</div>
        </div>

        <div className="stat-card">
          <div className="stat-value">{stats.totalViews}</div>
          <div className="stat-label">Lt xem</div>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboard;
