import React, { useState, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { ref, onValue, push, set, update, get } from 'firebase/database';
import { database } from '../firebase';
import { useAuth } from '../context/AuthContext';

const PostDetail = () => {
  const { id } = useParams();
  const { currentUser } = useAuth();
  const navigate = useNavigate();

  const [post, setPost] = useState(null);
  const [comments, setComments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [commentContent, setCommentContent] = useState('');
  const [userRating, setUserRating] = useState(0);
  const [hoverRating, setHoverRating] = useState(0);
  const [errorMessage, setErrorMessage] = useState('');

  useEffect(() => {
    // Ly chi tit bi vit
    const postRef = ref(database, `posts/${id}`);
    const postUnsubscribe = onValue(postRef, (snapshot) => {
      if (snapshot.exists()) {
        setPost({
          id,
          ...snapshot.val()
        });
      } else {
        // Bi vit khng tn ti
        navigate('/not-found');
      }
      setLoading(false);
    });

    // Ly danh sch bnh lun
    const commentsRef = ref(database, `comments/${id}`);
    const commentsUnsubscribe = onValue(commentsRef, (snapshot) => {
      const commentsData = snapshot.val() || {};
      const commentsArray = Object.keys(commentsData).map(commentId => ({
        id: commentId,
        ...commentsData[commentId]
      })).sort((a, b) => b.createdAt - a.createdAt);

      setComments(commentsArray);
    });

    // Kim tra xem ngi dng  nh gi bi vit cha
    if (currentUser) {
      const checkUserRating = async () => {
        const ratingsRef = ref(database, `ratings/${id}`);
        const snapshot = await get(ratingsRef);

        if (snapshot.exists()) {
          const ratingsData = snapshot.val();
          const userRatingEntry = Object.values(ratingsData).find(
            rating => rating.userId === currentUser.uid
          );

          if (userRatingEntry) {
            setUserRating(userRatingEntry.score);
          }
        }
      };

      checkUserRating();
    }

    return () => {
      postUnsubscribe();
      commentsUnsubscribe();
    };
  }, [id, navigate, currentUser]);

  const handleCommentSubmit = async (e) => {
    e.preventDefault();

    if (!currentUser) {
      navigate('/login');
      return;
    }

    if (!commentContent.trim()) {
      setErrorMessage('Vui lng nhp ni dung bnh lun');
      return;
    }

    try {
      const newCommentRef = push(ref(database, `comments/${id}`));

      await set(newCommentRef, {
        userId: currentUser.uid,
        userName: currentUser.displayName || 'Ngi dng n danh',
        content: commentContent,
        createdAt: Date.now(),
        parentCommentId: null
      });

      setCommentContent('');
      setErrorMessage('');
    } catch (error) {
      console.error('Li khi thm bnh lun:', error);
      setErrorMessage('C li xy ra khi ng bnh lun. Vui lng th li sau.');
    }
  };

  const handleRating = async (rating) => {
    if (!currentUser) {
      navigate('/login');
      return;
    }

    try {
      // Lu nh gi vo database
      const ratingData = {
        userId: currentUser.uid,
        score: rating,
        createdAt: Date.now()
      };

      // Kim tra xem ngi dng  nh gi trc  cha
      const ratingsRef = ref(database, `ratings/${id}`);
      const snapshot = await get(ratingsRef);

      if (snapshot.exists()) {
        const ratingsData = snapshot.val();
        const existingRatingKey = Object.keys(ratingsData).find(
          key => ratingsData[key].userId === currentUser.uid
        );

        if (existingRatingKey) {
          // Cp nht nh gi hin c
          await update(ref(database, `ratings/${id}/${existingRatingKey}`), {
            score: rating,
            createdAt: Date.now()
          });
        } else {
          // Thm nh gi mi
          await push(ref(database, `ratings/${id}`), ratingData);
        }
      } else {
        // Thm nh gi u tin
        await push(ref(database, `ratings/${id}`), ratingData);
      }

      // Cp nht trung bnh nh gi v s lng nh gi
      const updatedSnapshot = await get(ratingsRef);
      const ratings = updatedSnapshot.val() || {};
      const ratingValues = Object.values(ratings);
      const totalScore = ratingValues.reduce((sum, r) => sum + r.score, 0);
      const ratingCount = ratingValues.length;
      const averageRating = totalScore / ratingCount;

      await update(ref(database, `posts/${id}`), {
        averageRating,
        ratingCount
      });

      setUserRating(rating);
    } catch (error) {
      console.error('Li khi nh gi:', error);
      setErrorMessage('C li xy ra khi nh gi. Vui lng th li sau.');
    }
  };

  const formatDate = (timestamp) => {
    const date = new Date(timestamp);
    return date.toLocaleDateString('vi-VN', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  if (loading) {
    return (
      <div className="loading">
        <div className="spinner"></div>
      </div>
    );
  }

  return (
    <div className="post-detail-container">
      <article className="post-detail">
        <header className="post-detail-header">
          <h1 className="post-detail-title">{post.title}</h1>
          <div className="post-detail-meta">
            <span>Tc gi: {post.authorName}</span>
            <span>Danh mc: {post.category}</span>
            <span>Ngy ng: {formatDate(post.createdAt)}</span>
          </div>
        </header>

        {post.imageUrl && (
          <img src={post.imageUrl} alt={post.title} className="post-detail-image" />
        )}

        <div className="post-detail-content">
          {post.content.split('\n').map((paragraph, i) => (
            <p key={i}>{paragraph}</p>
          ))}
        </div>

        <div className="post-detail-meta">
          <span>Lt xem: {post.viewCount}</span>
          <span>nh gi: {post.averageRating ? `${post.averageRating.toFixed(1)}/5 (${post.ratingCount} lt)` : 'Cha c nh gi'}</span>
        </div>

        <div className="rating-section">
          <h3>nh gi bi vit</h3>
          <div className="rating">
            {[1, 2, 3, 4, 5].map((star) => (
              <span
                key={star}
                className={`star ${(hoverRating || userRating) >= star ? 'filled' : ''}`}
                onClick={() => handleRating(star)}
                onMouseEnter={() => setHoverRating(star)}
                onMouseLeave={() => setHoverRating(0)}
              >
                
              </span>
            ))}
          </div>
          {userRating > 0 && <p>nh gi ca bn: {userRating}/5</p>}
        </div>
      </article>

      <section className="comments-section">
        <h2 className="comments-header">Bnh lun ({comments.length})</h2>

        <form className="comment-form" onSubmit={handleCommentSubmit}>
          <div className="form-group">
            <label htmlFor="comment"> li bnh lun ca bn:</label>
            <textarea
              id="comment"
              value={commentContent}
              onChange={(e) => setCommentContent(e.target.value)}
              placeholder={currentUser ? "Nhp bnh lun ca bn..." : "ng nhp  bnh lun..."}
              disabled={!currentUser}
            ></textarea>
          </div>
          {errorMessage && <p className="error-message">{errorMessage}</p>}
          <button type="submit" disabled={!currentUser}>ng bnh lun</button>
        </form>

        <div className="comments-list">
          {comments.length > 0 ? (
            comments.map(comment => (
              <div key={comment.id} className="comment">
                <div className="comment-header">
                  <span className="comment-user">{comment.userName}</span>
                  <span className="comment-date">{formatDate(comment.createdAt)}</span>
                </div>
                <div className="comment-content">
                  {comment.content}
                </div>
              </div>
            ))
          ) : (
            <p>Cha c bnh lun no. Hy l ngi u tin bnh lun!</p>
          )}
        </div>
      </section>
    </div>
  );
};

export default PostDetail;
